const API_KEY = "52e700aa77f0211eac77ead33ba100bf";
const BASE_PATH = "https://api.themoviedb.org/3";

//  Each Playing Movies Interface
interface IMovie {
    id:number;
    backdrop_path:string;
    poster_path:string;
    title:string;
    overview:string;
}

//  Now Playing API Response Interface
export interface IGetMoviesResult {
    dates: {
        maximum:string;
        minimum:string;
    },
    page:number;
    results:IMovie[];
    total_pages:number;
    total_results:number;
}

export function getMovies() {
    return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`)
    .then(response => response.json()
    );
}